.step-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin: 0 auto;
  margin-top: 100px;
}
.step-container::before{
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
  height: 5px;
  background-color: #eee;
  content: '';
}
.progress{
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
  height: 5px;
  background-color: var(--color--);
  transition: all .4s cubic-bezier(0.19, 1, 0.22, .56);
}
.step-item{
  color: var(--color_font_gray--);
  background-color: var(--color_white--);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid var(--color_bg_gray--);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: 1;
}

.step-item.active{
  border-color: var(--color--);
  color: var(--color--);
}